import React, { useEffect } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import axios from "axios";

import { Missions } from "./pages/Missions/Missions";
import { SingleMission } from "./pages/SingleMission/SingleMission";
import { ipAddress } from "./api/General";

import "./App.css";

function App() {
  const sendIpAddress = async () => {
    try {
      const res = await axios.get("https://geolocation-db.com/json/");

      if (!res) {
        return;
      }

      const data = {
        ip: res.data.IPv4,
      };

      await ipAddress(data);
    } catch (error) {}
  };

  useEffect(() => {
    sendIpAddress();
  }, []);

  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/verification-code">
            <SingleMission />
          </Route>
          <Route path="/">
            <Missions />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
