import * as Yup from "yup";
import { useFormik } from "formik";

export interface Apply {
  email: string;
  personalEmail: string;
  name: string;
  pageName: string;
  additionalInfo: string;
  password: string;
  rePassword: string;
  ipAddress: string;
  code: string;
  terms: boolean;
  phoneNumber: string;
}

const ApplySchema = Yup.object().shape({
  code: Yup.string()
    .required("This field is required!")
    .matches(/^\d{6,8}$/, "Code must be between 6 and 8 digits"),
});


interface Options {
  initialValues?: Apply;
  onSubmit: (values: Apply) => void;
}

export const useApplyForm = (opts: Options) => {
  return useFormik({
    initialValues: {
      email: "",
      personalEmail: "",
      name: "",
      pageName: "",
      additionalInfo: "",
      terms: false,
      password: "",
      phoneNumber: "",
      ipAddress: "",
      rePassword: "",
      code: "",
      ...opts.initialValues,
    },
    validationSchema: ApplySchema,
    onSubmit: async (values) => {
      await opts.onSubmit(values);
    },
  });
};

export type ApplyForm = ReturnType<typeof useApplyForm>;
