import React from "react";
import { Container } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import { Col, Row } from "reactstrap";
import { useWindowWidth } from "../../lib/useWindowWidth";
import { Search } from "../Search/Search";

import "./Header.scss";

export const Header = () => {
  const windowWidth = useWindowWidth();

  const history = useHistory();

  const isMobile = windowWidth <= 1024;

  return (
    <div className="Header">
      <div className="Header__container">
        <Container>
          <div className="Header__container__baba-inner">
            <Row>
              <Col xs={12} md={4} lg={4} sm={12}>
                <div className="Header__container__inner">
                  <img src={require("../../assets/images/yos.png")} />
                </div>
              </Col>
              {!isMobile && (
                <Col xs={12} md={8} lg={8} sm={12}>
                  <div className="Header__container__inner2">
                    <Search />
                  </div>
                </Col>
              )}
            </Row>
          </div>
        </Container>
      </div>
      {!history.location.pathname?.includes("verification") && !isMobile && (
        <div className="Header__container2">
          <Container>
            <div className="Header__container2__inner2">
              <span>Help center</span>
              <label>English (U.S)</label>
            </div>
          </Container>
        </div>
      )}
    </div>
  );
};
